.hvideo
  &__video
    margin: 21rem 0 0 0
    &--mesure
      width: 100%
      max-height: 90vh
      object-fit: cover
      max-width: 1350px
      @include centerX
      display: block
  &__eyes
    margin: 5rem 0
    @include flex(center,center,row)
.hvalue
  &__container
    padding-top: 5rem
    padding-bottom: 10rem
    display: grid
    grid-template-columns: 1fr 1fr
  &__text
    & > h3
      margin-bottom: 1rem
    margin-right: 1rem
  &__icons

    margin-left: 5rem
    margin-top: 2rem
    display: grid
    grid-template-areas: "a a a b b b" "c c d d e e"
    & > img
      grid-area: auto/ span 2
    &--logos
      &-firts
        grid-area: a !important
      &-second
        grid-area: b !important
.hproyects
  &__articles
    display: flex
    flex-wrap: wrap
    @media only screen and (min-width: 768px)
      justify-content: space-around
    @media only screen and (min-width: 1112px)
      justify-content: space-between

    & > div.proyect
      &:nth-child(even)

        @media only screen and (min-width: 768px)
          margin-top: 2rem
        @media only screen and (min-width: 1112px)
          margin-top: 12rem
      &:nth-child(1), &:nth-child(4n) , &:nth-child(4n+1)
        & > a
          & > img.proyect__img
              width: 430px
              height: 590px
              object-fit: cover

  &__link
    display: block
    box-sizing: border-box
    margin: 10rem 0
    width: fit-content
.hpresentation
  background-color: $color__blue--light
  &__grid
    padding-top: 10rem
    display: grid
    grid-template-columns: 60% 40%
    &--text
      & > a
        margin-top: 4rem
        display: block
    &--dates
      margin-left: 134px
      width: 10rem
      & > a.link__button
              display: flex
              align-items: baseline
  &__photos

    @media only screen and (min-width: 768px)
      @include flex(space-around,center,row)
    @media only screen and (min-width: 1112px)
      @include flex(space-between,center,row)
    padding: 7rem 0 14rem 0
    &--ind
      border-radius: 1rem
      height: 386px
      @media only screen and (min-width: 768px)
        width: 45%
      @media only screen and (min-width: 1112px)
        width: 310px
    &--par
      border-radius: 1rem
      width: 308px
      height: 308px
      margin-top: 40px
      @media only screen and (min-width: 768px)
        display: none
      @media only screen and (min-width: 1112px)
        display: initial
.harticles
  margin: 6rem 0
  &__link
    margin-top: 5rem
    display: grid
    grid-template-columns: 20% 80%
    padding: 0 3rem
    &--grid
      grid-column: 2 / 3
      margin-left: 4rem

