.aproyect
  background-color: $color__white
  transition: background-color .4s cubic-bezier(0.165, 0.84, 0.44, 1)

  &__landing
    text-align: center
    &--anyo
      margin-top: 10.5rem
    &--title
      margin-top: 5rem
    &--summary
      margin-top: 5rem
    &--row
      margin-top: 5rem
    &--tags
      @include flex(center,initial,flex)
      margin-top: 1rem
      &-tag
        font-size: .75rem
        text-transform: uppercase
        padding: 0.25rem 0.625rem
        letter-spacing: .03125rem
        background-color: $color__blue--light
        line-height: initial
        margin-right: .5rem
        &:hover
          color: $color__blue--hover
  &__resumen

    margin-top: 8.75rem
    margin-bottom: 7rem
    &--firtsrow, &--secondrow, &--thirdrow
      @include flex(initial, initial, row)
      & > div
        & > h5
          margin-bottom: 1rem
          margin-top: 3rem
      & > div:first-child
        margin-right: 8.375rem
        width: 60%

      & > div:last-child
        width: 40%

    &--tool
      width: 7rem

  &__flex
    @include flex(initial,initial,column)
  &__cover
    &--photo
      max-height: 629px
      object-fit: cover
  &__content
    & > h2
      @include family("CY", 500)
      font-size: 46px
      letter-spacing: -.05px
      @media (max-width: 900px)
          font-size: 23px
      margin:  3.5rem 0 0 0
      max-width: 636px
      text-align: center
      @include centerX

    & > p
      @include family("Maxx", 400)
      font-size: 20px
      letter-spacing: 0
      margin:  3.5rem 0
      & > text
        max-width: 636px
      & > img
        max-height: 815px
        object-fit: cover
        max-width: 100%
  &__resto
    position: relative
    &--container
      min-height: 388px
      padding-top: 5rem
      box-sizing: border-box
      left: -10vw
      right: -10vw
      padding-bottom: 8rem
    &--center
      width: 1080px
      @include centerX
    &--proyects
      margin-top: 1rem
      @include flex(initial, initial,row)
      flex-wrap: wrap

.aiframes
    width: 100%
    max-height: 26rem
    padding-top: 5rem
    background-color: $color__blue--light
    padding-bottom: 5rem
    border-radius: 0.5rem
    margin-top: 5rem

