.article
  padding: 3rem 3rem 0 3rem
  position: relative
  &:hover
    & > a
      & > img.article__img
        opacity: 1
      & > h2.article__title
        color: $color--blue--hover
  &__container
    display: grid
    grid-template-columns: 20% 80%
  &__title
    color: $color--blue
    margin-left: 4rem
    z-index: 1
  &__day
    text-align: center
    @include flex(center,center,row)
    color: $color__grey
  &__img
    width: 344px
    height: 263px
    object-fit: cover
    position: absolute
    z-index: 0
    opacity: 0
    right: 0
    top: -5rem
    transition: 800ms cubic-bezier(0.165, 0.84, 0.44, 1)


