.proyect
  height: fit-content
  @media only screen and (min-width: 768px)
    padding: 0
  @media only screen and (min-width: 1112px)
    padding: 3rem 3rem 0 3rem
  @include transition-fast
  &:hover
    & > a
      & > img.proyect__img
        transform: scale(1.02)
        box-shadow: $shadow__main
    & > p.proyect__tag
      opacity: 1
  &__title
    margin-top: 0.5rem
  &__img
    width: 430px
    height: 430px
    object-fit: cover
    @include transition-fast
    cursor: url(../images/mouse__hover.png) 60 15, auto !important
  &__tag
    opacity: 0
    margin-top: -.5rem
    @include transition-fast
  &__tags
    &:hover
      color: $color__blue--hover
