.body-lock
    height: 100vh
    overflow: hidden

.overflow-visible
    overflow: visible

.invisible
    visibility: hidden

.display-none
    display: none

.not__visible
    @include noVisible(0, 14%, 1)
    &--second
        @include noVisible(0,40px,1)
        transition-delay: 1s
    &--third
        @include noVisible(0,40px,1)
        transition-delay: 2s
    &--fourty
        @include noVisible(0,40px,1)
        transition-delay: 3s
    &--proyect
        @include noVisible(0, 14%, 0.9)

.visible__now
    opacity: 1
    transform: translate(0px, 0px) scale(1)
    will-change: auto

.header__change
  background-color: #161616

.aproyect__change
  background-color: #161616
  color: $color__blue--light