.header
    position: fixed
    width: 100%
    background-color: $color__white
    z-index: 10
    box-shadow: $shadow__main
    opacity: 1
    backdrop-filter: blur(20px)
    -webkit-backdrop-filter: blur(20px)
    top: 0
    transition: background-color .4s cubic-bezier(0.165, 0.84, 0.44, 1)

    &__container
        margin: 0 auto
        display: flex
        box-sizing: border-box
        @media only screen and (min-width: 768px)
            padding: 2.03735144vw 2.5466893vw 0
            height: 6.28183362vw
        @media only screen and (min-width: 1276px)
            padding: 2.03735144vw 2.5466893vw 0
            height: 6.28183362vw
        @media only screen and (min-width: 1550px)
            padding: 31.58px 39.47px 0
            height: 97.37px
    &__logo
        width: 100%
        height: 100%
        display: flex
        &--img
            background-repeat: no-repeat
            background-image: url(../images/ruben-moya-logo.svg)
            background-position: center center
            background-size: 205px 44px
            @include transition-fast
            @media only screen and (min-width: 768px)
                transform: scale(4.3) translate(78px, 40px)
                padding: 16px 102.5px
            @media only screen and (min-width: 1276px)
                transform: scale(6) translate(85px, 30px)
                padding: 28px 102.5px
            @media only screen and (min-width: 1400px)
                transform: scale(6) translate(7vw, 30px)
                padding: 28px 102.5px
            &:hover
                background-image: url(../images/ruben-moya-logo-green.svg)
            & > span
                position: absolute
                overflow: hidden
                height: 1px
                width: 1px
                padding: 0
                border: 0
                top: 50%
        &--notrans
            transform: scale(1) translate(0, 0)
    &__menu
        display: flex
        align-items: center
        height: 100%
        z-index: 11
        &--ul
            display: grid
            grid-template-columns: repeat(4, 1fr)
            height: 100%
            & > li
                height: 100%
                width: 100%
                & > a
                    display: flex
                    align-items: center
                    margin: 0 1rem
                    text-transform: uppercase
                    height: 100%
                    width: 100%


