.ipresentation
  &__title
    margin: 225px 0 112px 0
    width: 746px
.iphoto
  &__img
    height: 615px
    object-fit: cover

.idescription
  background-color: $color__yellow
  &__container
    padding: 20rem 0 5rem 0
    margin: -15rem auto 0 auto
  &__description
    display: flex
    &--text
      width: 746px
      & > p
        margin-bottom: 2rem
    &--links
      @include flex(space-between, flex-start, row)
      margin-left: 24px
      width: 306px
      & > li
        & > a
          margin: 0
          & > i
            margin: 0
.ipowers
  &__container
    margin-top: 5rem
    margin-bottom: 5rem
    @include flex(space-between, initial, row)

  &__title
    width: 100%
    & > h5
      margin: 0
  &__first-column
    width: 100%
    &--title
      margin-bottom: 24px
    & > div
      margin-bottom: 40px
  &__second-column
    width: 100%
    &--long
      max-width: 419px
    &--title
      margin-bottom: 40px
    & > div
      margin-bottom: 40px
.inews
  background-color: $color__blue--light
  &__texts
    padding-top: 5rem
    display: grid
    grid-template-columns: 40% 60%
    &--label
      margin-bottom: 2rem
  &__photos
    @include flex(space-between,center,row)
    padding: 7rem 0 10rem 0
    &--ind
      border-radius: 1rem
      width: 310px
      height: 386px
    &--par
      border-radius: 1rem
      width: 308px
      height: 308px
      margin-top: 40px